/* 【APP 版本】Product Card 相關樣式 */
/*
 * APP 版本一律 @import 在 src/sass/app.scss 檔案內
*/

$gutter-s: rem-calc(10);
$gutter-l: rem-calc(53);
$gutter: $gutter-s;
$gutter-half: $gutter / 2;
$gutter-half-minus: $gutter-half * -1;

.pd-card {
	$root: &;

	$gutter: $gutter-s;
	$gutter-half: $gutter / 2;
	$gutter-half-minus: $gutter-half * -1;

	// 物件外層<li>
	// 100%寬內容
	&.grid-x {
		> #{$root} {
			&__item {
				&.cell.full {
					width: calc(100% - #{$gutter});

					#{$root} {
						&__title {
							height: auto;
						}

						&__img-box {
							&::before {
								@include before-img-size(840, 480);
							}
						}
					}
				}
			}
		}

		// 列表中間的廣告
		// XY Gird 因為設定很難蓋過，所以寫這
		& > .pd-list-banner {
			margin: 0 $gutter-half;
			margin-top: calc(var(--margin-y) - #{rem-calc(20)}); // 20 === 物件的 margin-bottom
			margin-bottom: var(--margin-y);
			width: calc(100% - #{$gutter});
		}
	}

	@include breakpoint(xlarge) {
		$gutter: $gutter-l;
		$gutter-half: $gutter / 2;
		$gutter-half-minus: $gutter-half * -1;

		&.grid-x {
			> #{$root} {
				&__item {
					&.cell.full {
						width: calc(100% - #{$gutter});
					}
				}
			}

			// 列表中間的廣告
			// XY Gird 因為設定很難蓋過，所以寫這
			& > .pd-list-banner {
				margin-right: $gutter-half;
				margin-left: $gutter-half;
				margin-bottom: calc(var(--margin-y) + #{(50px - 17)});
				width: calc(100% - #{$gutter});
			}
		}
	}
}

// --------------------------------------------------------

// 列表中間的廣告
.pd-list-banner {
	$root: &;

	--margin-y: #{rem-calc(30)};

	// margin-top: calc(var(--margin-y) - #{rem-calc(20)}); // 20 === 物件的 margin-bottom
	// margin-bottom: var(--margin-y);
	width: 100%;
	min-height: rem-calc(147);
	background: #e9f2f1 url('../images/index/pd-list-banner-bg-mask.svg') no-repeat left bottom / auto 100%;
	border: solid 1px #e3eceb;
	border-radius: rem-calc(20);

	// <a>
	&__link {
		--padding-y: #{rem-calc(18)};

		display: block;
		padding: var(--padding-y) rem-calc(16);

		// 恢復 Foundation 設定
		&,
		&:hover,
		&:focus {
			color: $body-font-color;
		}
	}

	&__cont {
		display: flex;
		align-items: center;
	}

	&__img-box {
		--width: #{rem-calc(100)};

		flex: 0 0 var(--width);
		width: var(--width);
	}

	&__text-box {
		flex: 1;
	}

	// 標題
	&__title {
		margin-bottom: rem-calc(4);
		font-weight: 600;
	}

	// 敘述
	&__desc {
		margin-bottom: rem-calc(8);
	}

	// Arrow
	&__more {
		margin: rem-calc(-9);
		margin-top: rem-calc(-9 - 4);
		font-weight: 600;
		color: $primary-color;

		.text-arrow {
			font-size: rem-calc(15);
		}
	}

	&.treasure {
		position: relative;

		#{$root} {
			&__link {
				padding-left: rem-calc(131);
			}

			&__img-box {
				position: absolute;
				top: 50%;
				left: 9px;
				margin-top: rem-calc(-56);
				width: 220px;
				background: url('../images/index/pd-list-banner_treasure@3x.png') no-repeat center center / contain;
				pointer-events: none;

				&::before {
					@include before-img-size(220, 138);
				}
			}
		}
	}
}

// --------------------------------------------------------

// 列表空值
.pd-list__empty {
	display: flex;
	align-items: center;
	justify-content: center;

	&.pd-list--publish {
		min-height: calc(100vh - #{rem-calc(89)}); // 44 + 45
	}

	.pd-list__info-box {
		position: relative;
		top: rem-calc(-30);
	}

	&-icon {
		margin-bottom: rem-calc(10);
		font-size: rem-calc(40);
		color: $bfGray;

		.be-icon {
			display: block;
		}
	}

	&-text {
		font-size: rem-calc(13);
		font-weight: $font-bold-m;
		line-height: 1.54;
		color: $color-tip-dark;
	}
}

