/* 【APP 版本】問與答 相關樣式 */
/*
 * APP 版本一律 @import 在 src/sass/app.scss 檔案內
*/

// Pd Card Fixed
%pd-card-fixed {
	.pd-card {
		position: fixed;
		z-index: 50;
		top: 44px;
		left: 0;
		width: 100%;
		border-top: 1px solid #f4f4f4;
		box-shadow: $shadow-panel;

		&__link {
			padding: rem-calc(10 19);
		}

		&__info-box {
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		// 綠色 Tag 隱藏
		&__tag-list {
			display: none;
		}

		&__img-box-wrapper {
			min-width: rem-calc(60);
		}

		&__title {
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
		}

		&__info-box {
			max-width: calc(100% - #{rem-calc(68px + 12px)});
		}

		&__price-box {
			text-align: left;
			justify-content: flex-start;
		}
	}
}

// 問與答 - 產品資訊
.pd-ask-intro {
	@extend %pd-card-fixed;

	padding-top: 0;
	padding-bottom: 0;

	&.sec-first {
		padding: 0;
	}

	// 隱藏返回
	.btn-back {
		display: none;
	}
}

// 問與答詳細內容
.pd-ask-detail {
	padding-top: rem-calc(80 + 20);
	// padding-bottom: rem-calc(56);

	// 隱藏列表「問與答」
	.sec-title-box {
		display: none;
	}

	.btn-load-more {
		// margin-bottom: rem-calc(24);
	}

	// 提問項目
	.pd-ask {
		&__bottom {
			position: fixed;
			z-index: 50;
			bottom: 0;
			left: 0;
			width: 100%;
			background: $white;
			box-shadow: $shadow-panel-bottom;
		}
	}

	.ask-input {
		padding: rem-calc(10 20);
	}
}

.ask-box {
	&__info {
		&__btn-more {
			display: inline-block;
		}
	}
}
