/* 【APP 版本】問與答 相關樣式 */
/*
 * APP 版本一律 @import 在 src/sass/app.scss 檔案內
*/

// 商品內頁
.app {
	.pd-cont-container {
		@include breakpoint(small down) {
			margin-top: 0;
		}

		.pd-other-info .pd-history {
			background-color: $content-bg-color;
		}

		&.sec-first {
			padding-top: 0;
		}
	}

	header.c2c ~ .main-container {
		.pd-fixed-info-space {
			height: 7.5rem;
		}
	}
}

// breadcrumbs
.breadcrumbs-nav {
	display: none !important;
}

// 我是賣家 - 交易履歷
.pd-other-info .pd-history {
	background-color: transparent;
}

// 詢問賣家
.pd-ask__bottom {
	display: block !important;
	border-top: 1px solid #f4f4f4;
}
