/* A.2 商品列表 */
.pd-list-global {
	$status-bar-height: 44; // iOS 有時間的 Bar 高度
	$nav-bars-height: 44; // iOS URL 高度
	$tab-bars-height: 49; // iOS 49

	display: flex;
	min-height: calc(100vh - #{$hd-height-s + rem-calc($status-bar-height + $nav-bars-height + $tab-bars-height)});

	.grid-container {
		display: flex;
		flex-direction: column;
	}

	.sec-title-box {
		width: calc(100vw - #{rem-calc(20 * 2)});
		
		@include breakpoint(medium) {
			width: calc(100vw - #{rem-calc(50 * 2)});
		}
	}

	.pd-list-null {
		display: flex;
		flex-direction: column;
		justify-content: center;
		flex: 1;
		margin: 10vh 0;
	}
}
