/* 【APP 版本】首頁 相關樣式 */
/*
 * APP 版本一律 @import 在 src/sass/app.scss 檔案內
*/

// -------------------------------------------------------

/* Loader */

// -------------------------------------------------------

/* Header - 主要 */
/* App Header（首頁沒有） */
.hd-app {
	$root: &;

	--padding-x: #{rem-calc(5)};

	@at-root %hd-normal {
		--color: #{$title-dark};
		--icon-color: #{$bfMediumGray};
		--bg: #{$white};
		--box-shadow: $shadow-panel;
	}

	// @extend %hd-normal;

	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	display: flex;
	justify-content: space-between;
	width: 100%;
	height: rem-calc(44);
	color: $title-dark;
	background: $white;
	box-shadow: $shadow-panel;

	// background: transparent;

	// 透明
	@at-root .hd.transparent {
		&:not(.is-collapse) {
			#{$root} {
				color: $white;
				background: transparent;
				box-shadow: none;

				.be-icon {
					color: $white;
				}
			}
		}
	}

	@at-root .hd.transparent-app {
		&:not(.is-collapse) {
			#{$root} {
				color: $white;
				background: transparent;
				box-shadow: none;

				.be-icon {
					color: $white;
				}
			}
		}
	}

	// 透明樣式
	// &.transparent {
	// 	--color: #{$white};
	// 	--icon-color: var(--color);
	// 	--bg: transparent;
	// 	--box-shadow: none;

	// 	html.is-collapse & {
	// 		@extend %hd-normal;
	// 	}

	// 	&.is-search-open {
	// 		@extend %hd-normal;

	// 		#{$root} {
	// 			&__btn-search {
	// 				&::before {
	// 					transform: scaleX(1);
	// 				}

	// 				.be-icon {
	// 					&::before {
	// 						transform: scale(0);
	// 					}
	// 				}
	// 			}
	// 		}
	// 	}
	// }

	&.hide-shadow {
		--box-shadow: none !important; // 為了蓋過上面(.hd.transparent.is-collapse)的設定，增加 !important
		box-shadow: none !important;
	}

	@at-root %hd-app-cell {
		.be-icon {
			color: $title-dark;
		}
	}

	@at-root %size {
		--size: calc(#{rem-calc(88)} + var(--padding-x));

		flex: 0 0 var(--size);
		width: var(--size);
	}

	&__l {
		@extend %hd-app-cell;
		@extend %size;

		padding-left: var(--padding-x);
	}

	// 中間區塊
	&__c {
		@extend %hd-app-cell;

		display: flex;
		align-items: center;
		justify-content: center;
		flex: 1;
		max-width: calc(100% - #{rem-calc(88 * 2)} - #{calc(var(--padding-x) * 2)});
		// max-width: 45%;
		line-height: $hd-height;
	}

	&__title {
		font-size: rem-calc(18);
		font-weight: $font-bold;
	}

	// 右側區塊
	&__r {
		@extend %hd-app-cell;
		@extend %size;

		padding-right: var(--padding-x);
		// text-align: right;
		// white-space: nowrap;
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}

	// 搜尋樣式
	&__btn-search {
		position: relative;

		&::before {
			--size: #{rem-calc(20)};

			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -1px;
			margin-left: calc(var(--size) / -2);
			width: var(--size);
			height: 2px;
			background: #999;
			transform: scaleX(0);
			transition: transform 0.3s;
		}

		.be-icon {
			&::before {
				transition: transform 0.3s;
			}
		}
	}

	&__btn-fav {
		position: relative;
		margin: 0;
	}

	&__btn-close {
		position: relative;
		height: rem-calc(44);

		.be-icon {
			position: absolute;
			top: 50%;
			left: 50%;
			color: #999;
			font-size: rem-calc(16);
			transform: translate(-50%, -50%);
		}
	}
}

.hd {
	// 必要時候隱藏 Shadow
	&.hide-shadow-app {
		@at-root %shadow-hidden-style {
			box-shadow: none !important;
		}

		@at-root %shadow-hidden {
			@extend %shadow-hidden-style;

			.hd-main {
				@extend %shadow-hidden-style;
			}

			.hd-app {
				@extend %shadow-hidden-style;
			}
		}

		@extend %shadow-hidden;

		&.is-collapse {
			@extend %shadow-hidden;
		}

		&.transparent {
			@extend %shadow-hidden;
		}
	}
}

// Header 隱藏 Web 介面
html:not(.page-index) {
	.hd-main {
		padding: 0;
		&__l,
		&__r {
			display: none;
		}
	}

	.hd-search {
		z-index: 1500;

		@include breakpoint(large) {
			// 假的，把Search收起來
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: $white;
			padding: rem-calc(20);
			animation: popup-from-top 0.3s;

			&:not(.is-open) {
				display: none;
			}

			&__top-box {
				display: flex;

				&__l {
					flex: 1;
				}

				&__r {
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}

			&__btn-close {
				display: block;
			}

			&__result {
				display: block;
				border: none;
			}
		}
	}
}

// -------------------------------------------------------

/* Footer */
.ft {
	display: none;
}

// -------------------------------------------------------

/* Other */

// 404 Page
.error-page {
	padding-top: 0 !important; // 為了蓋過 inline style 樣式
}

// Section
.app {
	.sec-first,
	.sec-first--large,
	.sec-first-final {
		margin-top: rem-calc(44);
		padding-top: rem-calc(20);
	}

	.sec-first--large {
		padding-top: rem-calc(40);
	}
}

// Breadcrumb
.breadcrumbs-wrapper {
	display: none !important;
}

// Go Top
.gotop {
	display: none;
}

// 於APP隱藏
.hide-for-app {
	display: none !important;
}

.show-for-app {
	display: block !important;
}

// 重置 web 區塊跟footer之間的距離
.sec.sec-bottom-space-for-web {
	margin-bottom: 0;
}
