// 購物車
.cart-cont-container {
	.btn-back {
		display: none;
	}

	.cart-comfirm__button {
		display: none;
	}

	// fixed
	.pd-fixed-info {
		display: block;

		.btn:last-child {
			margin-bottom: 0;
		}
	}

	&:not(.statue-success):not(.statue-fail) {
		.checkbox-group {
			margin-bottom: 0;
		}
	}
}

// 成功
.cart-cont-container.statue-success {
	.button-box {
		display: none;
	}
}

// 失敗
.cart-cont-container.statue-fail {
	.button-box {
		display: none;
	}
}

html.safari {
	.pd-fixed-info {
		padding-bottom: rem-calc(20);
	}
}
