/* 【APP 版本】首頁 相關樣式 */
/*
 * APP 版本一律 @import 在 src/sass/app.scss 檔案內
*/

// 首頁只有搜尋的Header
.page-index {
	// 隱藏 APP 版介面
	.hd-app {
		display: none !important;
	}

	.hd-main {
		@include breakpoint(medium down) {
			padding: 0;
		}

		&__l {
			display: none;
		}

		&__c {
			width: 100%;
		}

		&__r {
			display: none;
		}

		@include breakpoint(large) {
			justify-content: center;

			&__c {
				width: 80%;
				max-width: 600px;
			}
		}
	}

	.hd {
		&:not(.is-collapse) {
			.hd-search {
				&__input {
					&:not(:focus) {
						color: $white;
						background-color: rgba(#fdfdfd, 0.2);

						&::placeholder {
							color: $white;
						}

						// 前方 Search Icon
						~ .hd-search {
							&__btn-submit {
								color: $white;
							}
						}
					}
				}
			}
		}
	}

	.hd-search {
		display: block;

		&:not(.is-open) {
			position: relative;

			.hd-search {
				&__cont {
					background: transparent;

					@include breakpoint(medium down) {
						height: rem-calc(56);
					}
				}

				&__top-box {
					&__r {
						display: none;
					}
				}

				&__result {
					display: none;
				}
			}
		}

		&.is-open {
			.hd-search {
				&__input {
					&:not(:focus) {
						color: $primary-dark;
						background-color: #f4f4f4;
						border-color: transparent;

						&::placeholder {
							color: $input-placeholder-color;
						}

						~ .hd-search {
							&__btn-submit {
								color: #999;
							}
						}
					}
				}
			}
		}
	}
}

// 首頁最下方裝飾
.index-bottom-deco {
	margin-top: rem-calc(-44);
	width: 100%;
	background: url('../images/index/bottom-deco.svg');
	pointer-events: none;

	&::before {
		@include before-img-size(375, 78);
	}
}

// 首頁產品列表
.index-pd-list {
	// 隱藏「更多」
	&__btn-text {
		display: none;
	}

	// 顯示篩選
	&__btn-filter {
		display: block;
	}
}

// 沒有找到想要的寶物（Web Only）
.index-pd-more {
	display: none;
}
