/* 【APP 版本】E_我是買家 相關樣式 */
/*
 * APP 版本一律 @import 在 src/sass/app.scss 檔案內
*/

.pd-nav {
	+ .sec-first {
		margin-top: 0;
	}
}

.mem-menu-main {
	.mem-info {
		display: none;
	}
}

.mem-count {
	&__cell {
		padding: rem-calc(15 8);
	}
}

// 右側內容
.mem-cont {
	.sec-title-box {
		&__title {
			display: none;
		}
	}
}

.mem-record,
.mem-ask {
	.sec-title-box {
		display: none;
	}

	.pd-card--order {
		margin-bottom: 0;
	}
}

// 購買紀錄詳情頁
.order-detail-container {
	~ .pd-fixed-info-space {
		display: none;
	}
}

.mem-ask {
	.mem-tab > .page-nav {
		display: none;
	}
}

.statue-box__cover-img {
	width: rem-calc(189);
}
